.main {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    width: 100%;
    background: #232d31;
}

.container {
    width: 400px;
    height: 400px;
    position: relative;
    transform-style: preserve-3d;
    transform: perspective(1000px) rotateY(15deg) rotateX(15deg);
    animation: animate 15s linear infinite;
}

.container:hover .side {
    opacity: .5;
}

.container > :nth-child(1) {
    transform: translate3d(0,0,170px) rotateX(0);
}

.container > :nth-child(2) {
    transform: translate3d(0,-140px,-30px) rotateX(90deg);
}

.container > :nth-child(3) {
    transform: translate3d(-30px,-140px,0)rotateY(90deg) rotateX(90deg);
}

.block {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform-style: preserve-3d;
}

.block > :nth-child(1){
    transform: rotateY(0) translateX(0px) translateZ(0px);
}

.block > :nth-child(2){
    transform: rotateY(90deg) translateX(170px) translateZ(-170px);
}

.block > :nth-child(3){
    transform: rotateY(180deg) translateX(0px) translateZ(340px);
}

.block > :nth-child(1){
    transform: rotateY(270deg) translateX(-170px) translateZ(-170px);
}

.box {
    position: absolute;
    top: 50%;
    left: 0;
    transform-style: preserve-3d;
}

.shape {
    width: 400px;
    height: 60px;
}

.side {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #d8ffc9;
    box-shadow: inset 0 0 100px rgba(0,0,0,.5);
    transition: 0.5s;
    opacity: .9;
}

.shape :nth-child(1){
    transform: rotateX(0) translate3d(0,0,30px);
}

.shape :nth-child(2){
    transform: rotateX(90deg) translate3d(0,0,30px);
}

.shape :nth-child(3){
    transform: rotateX(180deg) translate3d(0,0,30px);
}

.shape :nth-child(4){
    transform: rotateX(270deg) translate3d(0,0,30px);
}

@keyframes animate {
    0% {
        transform: perspective(1000px) rotateX(15deg)
        rotateY(15deg);
    }
    100% {
        transform: perspective(1000px) rotateX(375deg)
        rotateY(375deg);
    }
}