body {
  background: #000;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

p {
  max-width: 80vw;
  text-align: justify;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes animate {
  from, to {
    filter: hue-rotate(0deg);
  }
  50% {
    filter: hue-rotate(360deg);
  }

}

@keyframes wave {
  0%{
    width: 0;
    height: 0;
    opacity: 0.5;
  }
  100%{
    width: 500px;
    height: 500px;
    opacity: 0;
  }
}

@keyframes loader {
  0% {
    transform: translateZ(-100px);
    width: 100%;
    height: 100%;
  }
  25% {
    transform: translateZ(100px);
    width: 100%;
    height: 100%;
  }
  50% {
    transform: translateZ(100px);
    width: 35%;
    height: 35%;
  }
  75% {
    transform: translateZ(-100px);
    width: 35%;
    height: 35%;
  }
  100% {
    transform: translateZ(-100px);
    width: 100%;
    height: 100%;
  }

}

@keyframes animate1 {
    0%{
      left: -100%;
    }
    100%,25%{
      left: 100%;
    }
}

@keyframes animate2 {
  0% {
    top:-100%;
  }
  100%,25% {
    top: 100%;
  }
}

@keyframes animate3 {
  0% {
    left:100%;
  }
  100%,25% {
    left: -100%;
  }
}

@keyframes animate4 {
  0% {
    top:100%;
  }
  100%,25% {
    top:-100%;
  }
}

@keyframes sqrt {
    0%,100% {
        transform: rotate(0deg);
    }
    50% {
      transform: rotate(360deg);
    }
}

@keyframes wavering {
    0%,100% {
      margin: 0 50px;
    }
    50% {
      margin: 0 -50px;
    }
}


@keyframes rotate1 {
  0% {
    transform: rotate(0) ;
  }
  100% {
    transform: rotate(720deg) ;
  }
}
@keyframes rotate2 {
  0% {
    transform: rotate(720deg) ;
  }
  100%{
    transform: rotate(0) ;
  }
}



::-webkit-scrollbar {
  width: 0;
}
