
.container {
    width: 100vw;
    height: 100vh;
    min-height: 750px;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
    --color: #a11466;
    background: var(--color);

}

.circle {
    border-radius: 50%;
    position: relative;
    width: 510px;
    height: 510px;
    box-sizing: border-box;
}

.side {
    border-bottom: 31px solid var(--color);
    border-top: 31px solid var(--color);
    border-left: 31px solid #fff;
    border-right: 31px solid #fff;
    box-sizing: border-box;
    border-radius: 50%;
    position: absolute;
    display: block;
    background: #fff;
}

.side:nth-child(1) {
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    animation: rotate 30s linear infinite;
}

.side:nth-child(2) {
    top: 30px;
    left: 30px;
    bottom: 30px;
    right: 30px;
    animation: rotate 15s linear infinite;
}

.side:nth-child(3) {
    top: 60px;
    left: 60px;
    bottom: 60px;
    right: 60px;
    animation: rotate 10s linear infinite;
}
.side:nth-child(4) {
    top: 90px;
    left: 90px;
    bottom: 90px;
    right: 90px;
    animation: rotate 7.5s linear infinite;
}

.side:nth-child(5) {
    top: 120px;
    left: 120px;
    bottom: 120px;
    right: 120px;
    animation: rotate 5s linear infinite;
}

.side:nth-child(6) {
    top: 150px;
    left: 150px;
    bottom: 150px;
    right: 150px;
    animation: rotate 3.75s linear infinite;
}

.side:nth-child(7) {
    top: 180px;
    left: 180px;
    bottom: 180px;
    right: 180px;
    animation: rotate 3s linear infinite;
}

.side:nth-child(8) {
    top: 210px;
    left: 210px;
    bottom: 210px;
    right: 210px;
    animation: rotate 2s linear infinite;
}

.side:nth-child(9) {
    top: 240px;
    left: 240px;
    bottom: 240px;
    right: 240px;
    border-bottom: 16px solid var(--color);
    border-top: 16px solid var(--color);
    border-left: 16px solid #fff;
    border-right: 16px solid #fff;
    animation: rotate 1s linear infinite;
}

@keyframes rotate {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}